<template>
  <!-- 5 -->
  <div class="swiper-slide SwiperFive">
    <div class="FiveBox">
      <div class="title">
        <h3></h3>
        <!--  -->
        <div class="ContactBox">
          <el-tooltip
            v-for="L in fiveList"
            :key="L.id"
            class="item"
            effect="dark"
            :content="L.content"
            placement="bottom-start"
          >
            <span
              class="iconfont"
              :class="[L.class, L.IconClass]"
              :data-clipboard-text="L.content"
              :style="{ color: L.color }"
              @click="LXW(L.click)"
            ></span>
          </el-tooltip>
          <!--  -->
        </div>
        <hr style="width: 80%; margin: 0 auto; marginbottom: 20px" />
        <p>
          <br /><br />IT
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard'
import throttle from '@//assets/throttle.js'
export default {
  name: 'FiveHtml',
  data () {
    return {
      // 
      fiveList: [
        {
          id: 0,
          content: 'S207789',
          color: '#28c445',
          class: 'tag-weChat',
          IconClass: 'icon-weixin',
          click: 'weChat'
        },
        {
          id: 1,
          content: '2250832184',
          color: '#30a5dd',
          class: 'tag-QQ',
          IconClass: 'icon-QQ',
          click: 'QQ'
        },
        {
          id: 2,
          content: '15216173039',
          color: '#ff9000',
          class: 'tag-Phone',
          IconClass: 'icon-dianhuatianchong',
          click: 'Phone'
        },
        {
          id: 3,
          content: '2250832184@qq.com',
          color: '#51a9ff',
          class: 'tag-email',
          IconClass: 'icon-youxiang',
          click: 'email'
        }
      ]
    }
  },
  methods: {
    LXW: throttle(function (method) {
      // console.log(method)
      if (method === 'weChat') {
        const clipboard = new Clipboard('.tag-weChat')
        clipboard.on('success', (e) => {
          this.$message.success('')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      } else if (method === 'QQ') {
        const clipboard = new Clipboard('.tag-QQ')
        clipboard.on('success', (e) => {
          this.$message.success('QQ')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      } else if (method === 'Phone') {
        const clipboard = new Clipboard('.tag-Phone')
        clipboard.on('success', (e) => {
          this.$message.success('')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      } else if (method === 'email') {
        const clipboard = new Clipboard('.tag-email')
        clipboard.on('success', (e) => {
          this.$message.success('')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      }
    }, 2000)
  }
}
</script>

<style lang="less" scoped>
// .
.SwiperFive {
  position: relative;
  background-color: #f2f2f2;
  .FiveBox {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    .title {
      text-align: center;
      width: 100%;
      font-size: 24px;
      .ContactBox {
        display: flex;
        max-width: 800px;
        min-width: 300px;
        margin: 0 auto;
        justify-content: space-around;
        margin: 50px auto;
      }
      p {
        padding: 20px;
        line-height: 32px;
      }
    }
  }
}
// 900px
@media screen and (max-width: 900px) {
  .SwiperFive {
    .FiveBox {
      .title {
        font-size: 22px;
        .ContactBox {
          margin: 40px auto;
        }
      }
    }
  }
}
// 600px
@media screen and (max-width: 600px) {
}
// 400px
@media screen and (max-width: 400px) {
  .iconfont {
    font-size: 52px;
  }
  .SwiperFive {
    .FiveBox {
      .title {
        font-size: 20px;
        .ContactBox {
          margin: 20px auto;
        }
      }
      p {
        font-size: 18px;
      }
    }
  }
}
</style>
